import React from 'react';
import { Slots, Slot } from './styledComponents';

const slots = [];
[17, 18, 19, 20, 21].forEach((slotHour) => {
  slots.push(`${slotHour}h - ${slotHour}h30`);
  slots.push(`${slotHour}h30 - ${slotHour + 1}h`);
});
slots.pop(); // remove the forbidden 21h30 - 22H slot

class SlotSelector extends React.Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <Slots>
        {slots.map(slot =>
          <Slot
            isSelected={this.state.slot === slot}
            key={slot}
            onClick={() => this.setState({ slot })}
          >{slot}</Slot>)}
      </Slots>
    );
  }
}

export default SlotSelector;



