import React, { Component } from 'react';
import { Link } from 'react-router';
import moment from 'moment';
import 'react-date-picker/index.css';
import { TransitionView, Calendar } from 'react-date-picker';

import { pricingInverse } from '../../pricing';
import SlotSelector from '../../components/SlotSelector';

import { mobileThreshold, Wrapper, Block, Logo, Header2, Title2, Button3, Dates, Center, ProgressBar, Progress,
  ProgressActive, ProgressDot, ProgressText } from '../../components/styledComponents';
import LogoImg from '../../assets/logo1.png';

class Step4 extends Component {
  static bounds() {
    return {
      mobile: {
        minWidth: 0,
        maxWidth: mobileThreshold,
      },
      desktop: {
        minWidth: mobileThreshold + 1,
      },
    };
  }

  constructor() {
    super();
    this.state = {
      orderItems: [],
      total: 0,
    };
  }

  addItem(slug) {
    this.setState({ orderItems: [...this.state.orderItems, { slug }] });
  }

  toggleAlteration(itemIndex, alterationId) {
    const newItem = { ...this.state.orderItems[itemIndex] };
    newItem.alterations = newItem.alterations || {};
    newItem.alterations[alterationId] = !newItem.alterations[alterationId];
    const newItems = [...this.state.orderItems];
    newItems[itemIndex] = newItem;

    const alterationPrice = pricingInverse[alterationId].price;
    const newTotal = newItem.alterations[alterationId] ? (this.state.total + alterationPrice) :
      (this.state.total - alterationPrice);
    this.setState({ orderItems: newItems, total: newTotal });
  }

  render() {
    return (
      <Wrapper>
        <Block>
          <Header2>
            <Logo src={LogoImg} />
            <ProgressBar>
              <Progress>
                <ProgressDot>
                  1
                </ProgressDot>
                <ProgressText>
                  Vetements
                </ProgressText>
              </Progress>
              <Progress>
                <ProgressDot>
                  2
                </ProgressDot>
                <ProgressText>
                  Retouches
                </ProgressText>
              </Progress>
              <ProgressActive>
                <ProgressDot>
                  3
                </ProgressDot>
                <ProgressText>
                  Mon RDV
                </ProgressText>
              </ProgressActive>
            </ProgressBar>
            <Title2>
              Mon Rendez-vous
            </Title2>
          </Header2>
          <Dates>
            <TransitionView>
              <Calendar
                dateFormat="DD/MM/YYYY"
                weekNumbers={false}
                defaultDate={moment().format('DD/MM/YYYY')}
                minDate={moment().format('DD/MM/YYYY')}
                locale="fr"
                weekStartDay={1}
                onChange={() => {}}
              />
            </TransitionView>
            <SlotSelector />
          </Dates>
          <Center>
            <Link to={'/order/step5'}><Button3>{'> Valider <'}</Button3></Link>
          </Center>
        </Block>
      </Wrapper>
    );
  }
}

export default Step4;



